.wrapper {
  margin: 0 auto;
  width: 100%;
  height: 100%;
}

h1 {
  margin-bottom: 50px;
  color: #50372b;
  font-size: 50px;
}

.block {
  background: linear-gradient(90deg, #f2cfcb, #ededed);
  padding-top: 70px;
  padding-bottom: 70px;
  max-height: 600px;
  max-width: 400px;
  border-radius: 30px;
  border: 5px solid #b39f7a;
  box-shadow: 4px 9px 30px 4px rgba(34, 60, 80, 0.61);
  margin: 0 auto;
}

.tempDisplay {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70%;
  padding: 10px;
}

.green {
  background-color: #01796f;
  width: 300px;
  height: 300px;
  margin-bottom: 20px;
  border-radius: 100%;
  border: 3px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 65px;
  transition: background 0.2s ease-in-out;
  box-shadow: 4px 9px 30px 4px rgba(34, 60, 80, 0.61);
}

.red {
  background-color: #f34723;
  width: 300px;
  height: 300px;

  border-radius: 100%;
  border: 3px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 65px;
  transition: background 0.2s ease-in-out;
  box-shadow: 4px 9px 30px 4px rgba(34, 60, 80, 0.61);
}
.cool {
  background-color: #4169e1;
  width: 300px;
  height: 300px;

  border-radius: 100%;
  border: 3px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 65px;
  transition: background 0.2s ease-in-out;
  box-shadow: 4px 9px 30px 4px rgba(34, 60, 80, 0.61);
}
.tempControl {
  display: flex;
  justify-content: center;
  align-items: center;
}

.tempMinus,
.tempPlus {
  background-color: rgb(67, 67, 68);
  width: 130px;
  height: 130px;
  border-radius: 100%;
  margin-right: 50px;
  border: 3px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 65px;
  cursor: pointer;
  user-select: none;
  box-shadow: 4px 9px 30px 4px rgba(34, 60, 80, 0.61);
}

.tempMinus:hover,
.tempPlus:hover {
  background-color: #3d2b1f;
}
.tempMinus:active,
.tempPlus:active {
  background-color: #755a57;
}

.tempMinus span,
.tempPlus span {
  transform: translateY(-10%);
}

.tempPlus {
  margin-right: 0;
}

@media (max-width: 400px) {
    h1 {
        margin-bottom: 15px;
        font-size: 27px;
    }

    .green, .red, .cool {
        width: 250px;
        height: 250px;
      }

      .tempMinus,
.tempPlus {
  width: 120px;
  height: 120px;
  margin-right: 25px;
}

.tempPlus {
    margin-right: 0;
}
}
